.header {
  /*border: 1px solid red;*/
  position: absolute;
  margin-top: -1cm;
  height: 2cm;
  width: 170mm;
  background-image: url('../img/header_logo_proximus.png');
  background-repeat: no-repeat;
}

.headerTitle {
  font-family: "Proximus-Regular", "Courier New", Courier, monospace;
  font-size: 12px;
  float: right;
  padding-top: 0cm;
}

.header.index > .headerTitle {
  border: 1px solid grey;
  padding: 10px;
  font-size: 14px;
  color: grey;
}

.footer {
  /*border: 1px solid red;*/
  position: absolute;
  margin-top: -1cm;
  height: 2cm;
  width: 170mm;
  background-image: none;
}

/*.footer.index {
    background-image: url('./img/header_logo_proximus.png');
    background-repeat: no-repeat;
    background-position: right;
}*/

.footerTitle {
  font-family: "Proximus-Bold", "Courier New", Courier, monospace;
  font-size: 12px;
}

.footerText {
  font-family: "Proximus-Regular", "Courier New", Courier, monospace;
  font-size: 9px;
  float: left;
}

.footerPagenumber {
  font-family: "Proximus-Regular", "Courier New", Courier, monospace;
  font-size: 9px;
  float: right;
}

.existing-telco > table,
.existing-telco > table > tr,
.existing-telco > table > tr > td {
  padding: 0;
  margin: 0;
}

.existing-telco > table > tbody > tr > td {
  border: 1px dotted gray;
}

.existing-telco > table > tbody > tr > th {
  border: 1px solid rgb(92, 45, 145);
  color: rgb(92, 45, 145);
}

@page {
  size: A4;
  margin: 0;
}

@media print {
  html, body {
    width: 210mm;
    height: 297mm;
  }

  .page {
    margin: 0;
    border: initial;
    border-radius: initial;
    width: initial;
    min-height: initial;
    box-shadow: initial;
    background: initial;
    page-break-after: always;
  }
}